exports.push([module.id, "\n.el-form-item__content .el-button[data-v-0b11f2a2] {\r\n  margin-top: 20px;\n}\n.el-table--border th[data-v-0b11f2a2] {\r\n  text-align: center;\n}\n.el-table th .cell[data-v-0b11f2a2] {\r\n  text-align: center;\n}\n.el-form--inline .el-form-item[data-v-0b11f2a2] {\r\n  margin-right: 2px;\n}\n.el-form-item[data-v-0b11f2a2] {\r\n  margin-bottom: 2px;\n}\n.el-dialog__footer[data-v-0b11f2a2] {\r\n  padding: 10px 0px;\r\n  text-align: center;\n}\n.check-all-select[data-v-0b11f2a2] {\r\n  padding: 10px;\n}\n.check-all-select span.text-all[data-v-0b11f2a2] {\r\n  font-size: 14px;\r\n  font-weight: bold;\n}\n.check-all-select label[data-v-0b11f2a2] {\r\n  float: right;\n}\n.mb-10[data-v-0b11f2a2] {\r\n  margin-bottom: 10px;\n}\n.show[data-v-0b11f2a2] {\r\n  display: block;\n}\n.hidden[data-v-0b11f2a2] {\r\n  display: none;\n}\r\n", "",{"version":3,"sources":["src/views/contents/market/apiDetail.vue"],"names":[],"mappings":";AAwNA;EACA,gBAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,iBAAA;AACA;AAEA;EACA,kBAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;AACA;AAEA;EACA,eAAA;EACA,iBAAA;AACA;AAEA;EACA,YAAA;AACA;AAEA;EACA,mBAAA;AACA;AACA;EACA,cAAA;AACA;AACA;EACA,aAAA;AACA","file":"apiDetail.vue?vue&type=style&index=0&id=0b11f2a2&scoped=true&lang=css&","sourcesContent":["<!--\r\n * @Description: API渠道总计\r\n * @Author: 焦丙乾(jiaobingqian)\r\n * @Email: jiaobq123@163.com\r\n * @Date: 2019-07-15 18:41:38\r\n * @LastEditTime: 2019-07-18 16:29:22\r\n * @LastEditors: 王娇(wangjiao)\r\n -->\r\n<template>\r\n  <div>\r\n    <tab-page ref=\"tabPage\">\r\n      <template slot=\"apiStatisticsDetail\">\r\n        <data-table @exportDataSuccess=\"exportDataSuccess\" :data-table=\"dataTable\" ref=\"dataTable\">\r\n          <div slot=\"conditions\" style=\"margin-bottom:15px\">\r\n            <el-form :inline=\"true\" :model=\"queryParams\">\r\n              <el-form-item>\r\n                <el-date-picker\r\n                  v-model=\"queryParams.beginTime\"\r\n                  type=\"date\"\r\n                  placeholder=\"选择日期时间\"\r\n                  size=\"small\"\r\n                  style=\"width:170px\"\r\n                ></el-date-picker>\r\n              </el-form-item>\r\n              <span class=\"span-style\">至</span>\r\n              <el-form-item>\r\n                <el-date-picker\r\n                  v-model=\"queryParams.endTime\"\r\n                  type=\"date\"\r\n                  placeholder=\"选择日期时间\"\r\n                  size=\"small\"\r\n                  style=\"width:170px\"\r\n                ></el-date-picker>\r\n              </el-form-item>\r\n              <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.intervalMinutes\"\r\n                  placeholder=\"分组时间间隔\"\r\n                  size=\"small\"\r\n                  clearable\r\n                >\r\n                  <el-option key=\"10\" label=\"10(min)\" value=\"10\"></el-option>\r\n                  <el-option key=\"20\" label=\"20(min)\" value=\"20\"></el-option>\r\n                  <el-option key=\"30\" label=\"30(min)\" value=\"30\"></el-option>\r\n                  <el-option key=\"40\" label=\"40(min)\" value=\"40\"></el-option>\r\n                  <el-option key=\"50\" label=\"50(min)\" value=\"50\"></el-option>\r\n                  <el-option key=\"60\" label=\"60(min)\" value=\"60\"></el-option>\r\n                </el-select>\r\n              </el-form-item>\r\n              <!-- <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.device\"\r\n                  clearable\r\n                  filterable\r\n                  placeholder=\"设备\"\r\n                  size=\"small\"\r\n                >\r\n                  <el-option\r\n                    v-for=\"item in deviceOptions\"\r\n                    :key=\"item.value\"\r\n                    :label=\"item.label\"\r\n                    :value=\"item.value\"\r\n                  ></el-option>\r\n                </el-select>\r\n              </el-form-item>-->\r\n              <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.source\"\r\n                  ref=\"sourceSelect\"\r\n                  clearable\r\n                  filterable\r\n                  remote\r\n                  :remote-method=\"remoteSourceMethod\"\r\n                  :loading=\"loading\"\r\n                  placeholder=\"source\"\r\n                  size=\"small\"\r\n                >\r\n                  <el-option\r\n                    v-for=\"item in sourceOptions\"\r\n                    :key=\"item.value\"\r\n                    :label=\"item.label\"\r\n                    :value=\"item.value\"\r\n                  ></el-option>\r\n                </el-select>\r\n              </el-form-item>\r\n              <br />\r\n              <!-- <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.medium\"\r\n                  ref=\"mediumSelect\"\r\n                  clearable\r\n                  filterable\r\n                  remote\r\n                  :remote-method=\"remoteMediumMethod\"\r\n                  :loading=\"loading\"\r\n                  placeholder=\"medium\"\r\n                  size=\"small\"\r\n                >\r\n                  <el-option\r\n                    v-for=\"item in mediumOptions\"\r\n                    :key=\"item.value\"\r\n                    :label=\"item.label\"\r\n                    :value=\"item.value\"\r\n                  ></el-option>\r\n                </el-select>\r\n              </el-form-item>\r\n              <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.campaign\"\r\n                  ref=\"campaignSelect\"\r\n                  clearable\r\n                  filterable\r\n                  remote\r\n                  :remote-method=\"remoteCampaignMethod\"\r\n                  :loading=\"loading\"\r\n                  placeholder=\"campaign\"\r\n                  size=\"small\"\r\n                >\r\n                  <el-option\r\n                    v-for=\"item in campaignOptions\"\r\n                    :key=\"item.value\"\r\n                    :label=\"item.label\"\r\n                    :value=\"item.value\"\r\n                  ></el-option>\r\n                </el-select>\r\n              </el-form-item>-->\r\n              <el-form-item>\r\n                <el-select\r\n                  v-model=\"queryParams.isksjk\"\r\n                  clearable\r\n                  filterable\r\n                  placeholder=\"快速借款\"\r\n                  size=\"small\"\r\n                >\r\n                  <el-option\r\n                    v-for=\"item in ksjkOptions\"\r\n                    :key=\"item.value\"\r\n                    :label=\"item.label\"\r\n                    :value=\"item.value\"\r\n                  ></el-option>\r\n                </el-select>\r\n              </el-form-item>\r\n              <el-form-item>\r\n                <el-button type=\"primary\" @click=\"onSubmit\" size=\"small\" style=\"margin-top:0\">查询</el-button>\r\n              </el-form-item>\r\n            </el-form>\r\n            <el-button\r\n              class=\"right mb-10\"\r\n              type=\"primary\"\r\n              size=\"small\"\r\n              icon=\"el-icon-document\"\r\n              @click=\"exportExcel\"\r\n            >导出excel</el-button>\r\n            <div\r\n              style=\"width:90%;margin-top:10px;\"\r\n              :class=\"{hidden:!showProgress, show:showProgress}\"\r\n            >\r\n              <el-progress\r\n                class=\"process-item\"\r\n                :color=\"customerColor\"\r\n                :stroke-width=\"10\"\r\n                :percentage=\"downloadPercentage\"\r\n                :status=\"downloadStatus\"\r\n              ></el-progress>\r\n            </div>\r\n          </div>\r\n          <template slot=\"columns\">\r\n            <el-table-column type=\"index\" label=\"序号\" width=\"60\"></el-table-column>\r\n            <el-table-column sortable prop=\"date\" label=\"日期\" width=\"120\"></el-table-column>\r\n\r\n            <!-- <el-table-column sortable prop=\"plat\" label=\"设备\" width=\"100\"></el-table-column> -->\r\n            <el-table-column sortable prop=\"ksjk_existing\" label=\"快速借款用户\" width=\"90\"></el-table-column>\r\n            <el-table-column sortable prop=\"source\" label=\"source\"></el-table-column>\r\n            <!-- <el-table-column sortable prop=\"medium\" label=\"medium\"></el-table-column>\r\n            <el-table-column sortable prop=\"campaign\" label=\"campaign\"></el-table-column>\r\n            <el-table-column sortable prop=\"uv\" label=\"uv\"></el-table-column>\r\n            <el-table-column sortable prop=\"phones_count\" label=\"手机号\"></el-table-column>\r\n            <el-table-column sortable prop=\"take_phone_new\" label=\"留手机号新用户\"></el-table-column>-->\r\n            <el-table-column sortable prop=\"register_count\" label=\"注册\"></el-table-column>\r\n            <el-table-column sortable prop=\"activation_count\" label=\"激活\"></el-table-column>\r\n            <el-table-column sortable prop=\"jinjian\" label=\"当日进件\"></el-table-column>\r\n            <el-table-column sortable prop=\"reject\" label=\"当日拒绝\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval\" label=\"当日审核\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval_low\" label=\"当日审核(额度<=1000)\" width=\"125\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval_high\" label=\"当日审核(额度>1000)\" width=\"120\"></el-table-column>\r\n            <el-table-column sortable prop=\"emitCn\" label=\"当日放款\"></el-table-column>\r\n            <el-table-column sortable prop=\"emitMoney\" label=\"放款金额\"></el-table-column>\r\n            <el-table-column sortable prop=\"act_reg_rate\" label=\"激活/注册\" width=\"90\"></el-table-column>\r\n            <el-table-column sortable prop=\"jj_jh_rate\" label=\"进件/激活\" width=\"90\"></el-table-column>\r\n            <el-table-column sortable prop=\"jj_reg_rate\" label=\"进件/注册\" width=\"90\"></el-table-column>\r\n            <el-table-column sortable prop=\"review_jinj_rate\" label=\"审核/进件\" width=\"90\"></el-table-column>\r\n            <el-table-column\r\n              sortable\r\n              prop=\"review_low_jinj_rate\"\r\n              label=\"审核/进件(额度<=1000)\"\r\n              width=\"130\"\r\n            ></el-table-column>\r\n            <el-table-column\r\n              sortable\r\n              prop=\"review_high_jinj_rate\"\r\n              label=\"审核/进件(额度>1000)\"\r\n              width=\"120\"\r\n            ></el-table-column>\r\n            <el-table-column sortable prop=\"jinjian_t7\" label=\"T7进件\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval_t7\" label=\"T7审核\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval_t7_low\" label=\"T7审核(额度<=1000)\" width=\"120\"></el-table-column>\r\n            <el-table-column sortable prop=\"approval_t7_high\" label=\"T7审核(额度>1000)\" width=\"120\"></el-table-column>\r\n            <el-table-column sortable prop=\"emitCn_t7\" label=\"T7放款\"></el-table-column>\r\n            <el-table-column sortable prop=\"emitMoney_t7\" label=\"T7放款金额\"></el-table-column>\r\n          </template>\r\n        </data-table>\r\n      </template>\r\n    </tab-page>\r\n  </div>\r\n</template>\r\n<style scoped>\r\n.el-form-item__content .el-button {\r\n  margin-top: 20px;\r\n}\r\n\r\n.el-table--border th {\r\n  text-align: center;\r\n}\r\n\r\n.el-table th .cell {\r\n  text-align: center;\r\n}\r\n\r\n.el-form--inline .el-form-item {\r\n  margin-right: 2px;\r\n}\r\n\r\n.el-form-item {\r\n  margin-bottom: 2px;\r\n}\r\n\r\n.el-dialog__footer {\r\n  padding: 10px 0px;\r\n  text-align: center;\r\n}\r\n\r\n.check-all-select {\r\n  padding: 10px;\r\n}\r\n\r\n.check-all-select span.text-all {\r\n  font-size: 14px;\r\n  font-weight: bold;\r\n}\r\n\r\n.check-all-select label {\r\n  float: right;\r\n}\r\n\r\n.mb-10 {\r\n  margin-bottom: 10px;\r\n}\r\n.show {\r\n  display: block;\r\n}\r\n.hidden {\r\n  display: none;\r\n}\r\n</style>\r\n<script>\r\nimport common from \"../../../utils/common\";\r\nimport formatDate from \"../../../utils/formatDate\";\r\nimport DataTable from \"../../../components/DataTable\";\r\nimport TabPage from \"../../../components/TabPage\";\r\nimport { Notification } from \"element-ui\";\r\nexport default {\r\n  components: {\r\n    DataTable,\r\n    TabPage,\r\n  },\r\n  data() {\r\n    return {\r\n      sourceCheckAll: false,\r\n      mediumCheckAll: false,\r\n      campaignCheckAll: false,\r\n      deviceCheckAll: false,\r\n\r\n      sourceOptions: [],\r\n      mediumOptions: [],\r\n      campaignOptions: [],\r\n      sourceOptionsList: [],\r\n      mediumOptionsList: [],\r\n      campaignOptionsList: [],\r\n      deviceOptions: [\r\n        {\r\n          value: \"iOS\",\r\n          label: \"iOS\",\r\n        },\r\n        {\r\n          value: \"Android\",\r\n          label: \"Android\",\r\n        },\r\n      ],\r\n      ksjkOptions: [\r\n        {\r\n          value: \"是\",\r\n          label: \"是\",\r\n        },\r\n        {\r\n          value: \"否\",\r\n          label: \"否\",\r\n        },\r\n      ],\r\n      initBeginTime: \"\",\r\n      initEndTime: \"\",\r\n\r\n      queryParams: {\r\n        source: \"\",\r\n        medium: \"\",\r\n        campaign: \"\",\r\n        intervalMinutes: \"\",\r\n        // device: \"\",\r\n        isksjk: \"\",\r\n        beginTime: \"\", // 开始时间\r\n        endTime: \"\", // 结束时间\r\n      },\r\n      dataTable: {\r\n        pageSize: 50,\r\n        pageSizes: [10, 20, 50, 100, 1000, 5000, 10000, 20000],\r\n        url: \"/web/v4/market/queryApiDetail\", // 代付管理\r\n        emptyText: \"没有记录\",\r\n        showSummary: true,\r\n        summaryMethod: this.getSummaries,\r\n      },\r\n      loading: false,\r\n      downloadPercentage: 0,\r\n      downloadStatus: \"warning\",\r\n      showProgress: false,\r\n      customerColor: \"#409eff\",\r\n    };\r\n  },\r\n  mounted() {\r\n    this.dataTable.queryParams = this.queryParams;\r\n    let date = new Date();\r\n    let beginDate = new Date(date.setDate(date.getDate() - 7));\r\n    this.initBeginTime = this.dealDate(beginDate);\r\n    this.initEndTime = this.dealDate(new Date());\r\n    this.dataTable.queryParams.beginTime = this.initBeginTime + \" 00:00:00\";\r\n    this.dataTable.queryParams.endTime = this.initEndTime + \" 23:59:59\";\r\n\r\n    var defaultPage = {\r\n      label: \"API渠道总计\",\r\n      name: \"apiStatisticsDetail\",\r\n      slotName: \"apiStatisticsDetail\",\r\n      closable: false,\r\n    };\r\n    this.$refs.tabPage.$emit(\"open-tab\", defaultPage);\r\n\r\n    // this.getCampaigns();\r\n    // this.getMediums();\r\n    this.getSources();\r\n  },\r\n  beforeDestroy() {\r\n    // this.campaignOptions = [];\r\n    // this.mediumOptions = [];\r\n    // this.sourceOptions = [];\r\n    // this.$refs.campaignSelect.doDestroy();\r\n    // this.$refs.mediumSelect.doDestroy();\r\n    // this.$refs.sourceSelect.doDestroy();\r\n  },\r\n  methods: {\r\n    remoteSourceMethod(query) {\r\n      if (query !== \"\") {\r\n        this.loading = true;\r\n        setTimeout(() => {\r\n          this.loading = false;\r\n\r\n          this.sourceOptions = this.sourceOptionsList.filter(item => {\r\n            return item.name.indexOf(query) > -1;\r\n          });\r\n        }, 200);\r\n      } else {\r\n        this.sourceOptions = [];\r\n      }\r\n    },\r\n    remoteMediumMethod(query) {\r\n      if (query !== \"\") {\r\n        this.loading = true;\r\n        setTimeout(() => {\r\n          this.loading = false;\r\n\r\n          this.mediumOptions = this.mediumOptionsList.filter(item => {\r\n            return item.name.indexOf(query) > -1;\r\n          });\r\n        }, 200);\r\n      } else {\r\n        this.mediumOptions = [];\r\n      }\r\n    },\r\n    remoteCampaignMethod(query) {\r\n      if (query !== \"\") {\r\n        this.loading = true;\r\n        setTimeout(() => {\r\n          this.loading = false;\r\n\r\n          this.campaignOptions = this.campaignOptionsList.filter(item => {\r\n            return item.name.indexOf(query) > -1;\r\n          });\r\n        }, 200);\r\n      } else {\r\n        this.campaignOptions = [];\r\n      }\r\n    },\r\n    loadLists() {\r\n      this.$refs.dataTable.$emit(\"load-data\");\r\n    },\r\n    onSubmit: function() {\r\n      this.dataTable.queryParams = this.queryParams;\r\n      // this.dataTable.queryParams.pageSize = 50;\r\n      var tag = this.warnTime(\r\n        this.dataTable.queryParams.beginTime,\r\n        this.dataTable.queryParams.endTime,\r\n      );\r\n      if (tag) {\r\n        return;\r\n      }\r\n      this.dataTable.queryParams.beginTime =\r\n        this.dealDate(this.dataTable.queryParams.beginTime) + \" 00:00:00\";\r\n      this.dataTable.queryParams.endTime =\r\n        this.dealDate(this.dataTable.queryParams.endTime) + \" 23:59:59\";\r\n      this.dataTable.url = \"/web/v4/market/queryApiDetail\";\r\n      this.$refs.dataTable.$emit(\"load-data\");\r\n    },\r\n    warnTime(startTime, endTime) {\r\n      if (!startTime) {\r\n        this.$notify({\r\n          message: \"查询开始时间不能为空！\",\r\n          type: \"warning\",\r\n        });\r\n        return true;\r\n      }\r\n      if (!endTime) {\r\n        this.$notify({\r\n          message: \"查询结束时间不能为空！\",\r\n          type: \"warning\",\r\n        });\r\n        return true;\r\n      }\r\n      let timeStart = new Date(startTime).getTime();\r\n      let timeEnd = new Date(endTime).getTime();\r\n      if (timeStart > timeEnd) {\r\n        this.$notify({\r\n          message: \"查询开始时间不能大于结束时间\",\r\n          type: \"warning\",\r\n        });\r\n        return true;\r\n      }\r\n      return false;\r\n    },\r\n    dealTime(time) {\r\n      return formatDate(new Date(time), \"yyyy-MM-dd hh:mm:ss\");\r\n    },\r\n    formatterDate(row, column) {\r\n      return formatDate(row.date, \"yyyy-MM-dd\");\r\n    },\r\n    dealDate(date) {\r\n      return formatDate(new Date(date), \"yyyy-MM-dd\");\r\n    },\r\n    getSummaries(param) {\r\n      const { columns, data } = param;\r\n      const sums = [];\r\n      columns.forEach((column, index) => {\r\n        console.log(\"column:\", column.property);\r\n        if (index === 0) {\r\n          sums[index] = \"总计\";\r\n          return;\r\n        }\r\n        var values;\r\n        var itemProperty = column.property;\r\n        if (\r\n          itemProperty === \"act_reg_rate\" ||\r\n          itemProperty === \"jj_jh_rate\" ||\r\n          itemProperty === \"jj_reg_rate\" ||\r\n          itemProperty === \"review_jinj_rate\" ||\r\n          itemProperty === \"review_low_jinj_rate\" ||\r\n          itemProperty === \"review_high_jinj_rate\"\r\n        ) {\r\n          values = data.map(item => {\r\n            if (item[column.property] != null) {\r\n              return Number(item[column.property].replace(\"%\", \"\"));\r\n            }\r\n          });\r\n        } else {\r\n          values = data.map(item => Number(item[column.property]));\r\n        }\r\n\r\n        if (!values.every(value => isNaN(value))) {\r\n          sums[index] = values.reduce((prev, curr) => {\r\n            const value = Number(curr);\r\n            if (!isNaN(value)) {\r\n              return prev + curr;\r\n            } else {\r\n              return prev;\r\n            }\r\n          }, 0);\r\n          if (\r\n            itemProperty === \"act_reg_rate\" ||\r\n            itemProperty === \"jj_jh_rate\" ||\r\n            itemProperty === \"jj_reg_rate\" ||\r\n            itemProperty === \"review_jinj_rate\" ||\r\n            itemProperty === \"review_low_jinj_rate\" ||\r\n            itemProperty === \"review_high_jinj_rate\"\r\n          ) {\r\n            sums[index] = (sums[index] / data.length).toFixed(2) + \" %\";\r\n          }\r\n        } else {\r\n          sums[index] = \"N/A\";\r\n        }\r\n      });\r\n\r\n      return sums;\r\n    },\r\n    exportExcel() {\r\n      this.dataTable.queryParams = this.queryParams;\r\n      var tag = this.warnTime(\r\n        this.dataTable.queryParams.beginTime,\r\n        this.dataTable.queryParams.endTime,\r\n      );\r\n      if (tag) {\r\n        return;\r\n      }\r\n      // console.log(Object.prototype.toString.call(this.dataTable.queryParams.beginTime));\r\n      this.dataTable.queryParams.beginTime =\r\n        this.dealDate(this.dataTable.queryParams.beginTime) + \" 00:00:00\";\r\n      this.dataTable.queryParams.endTime =\r\n        this.dealDate(this.dataTable.queryParams.endTime) + \" 23:59:59\";\r\n      var exportParams = {};\r\n      Object.assign(exportParams, this.dataTable.queryParams);\r\n      exportParams.pageSize = this.$refs.dataTable.total;\r\n      exportParams.page = 1;\r\n      this.$refs.dataTable.$emit(\"export-data\", exportParams);\r\n    },\r\n    exportDataStatus(statusInfo) {\r\n      if (statusInfo.status == 2) {\r\n        this.downloadPercentage = 100;\r\n        this.downloadStatus = \"success\";\r\n      } else if (statusInfo.status == -1) {\r\n        this.downloadStatus = \"exception\";\r\n      } else {\r\n        this.downloadPercentage = statusInfo.percent;\r\n        console.log(statusInfo.percent);\r\n        console.log(this.downloadPercentage);\r\n        this.showProgress = true;\r\n      }\r\n    },\r\n    exportDataSuccess(statusInfo) {\r\n      require.ensure([], () => {\r\n        const { export_json_to_excel } = require(\"vendor/Export2Excel\");\r\n        const tHeader = [\r\n          \"日期\",\r\n          // \"设备\",\r\n          \"快速借款用户\",\r\n          \"source\",\r\n          // \"medium\",\r\n          // \"campaign\",\r\n          // \"uv\",\r\n          // \"手机号\",\r\n          // 